body {
  background-color: black!important;
  color: white!important;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media only screen and (max-width: 450px) {
  body {
    background-color: rgba(0,0,0,0.925)!important;
  }
}
/* ScrollBar */
::-webkit-scrollbar {
  display: none!important;
}
/* Track */
::-webkit-scrollbar-track {
  color: black;
  background-color: black;
}
/* Handle */
::-webkit-scrollbar-thumb {
  color: black;
  background-color: black;
}
/* --- top scroll bar --- */
.myScrollBar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
  animation: 14s fade-in;
}
@keyframes fade-in {
  0% {opacity: 0;}
  50% {opacity: 0;}
  100% {opacity: 1;}
}
/* ---Main-App-Component--- */
.mainView {
  margin-bottom: 50vh;
}
.mainScreen:before {
  content: "";
  background-image: url('./images/city-1.jpg');
  background-size: cover;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: -1;
  height: 100vh;
  width: 100%;
  mask-image: linear-gradient(to bottom,
  rgba(0,0,0,1),
  rgba(0,0,0,1),
  rgba(0,0,0,0.5),
  rgba(0,0,0,0));
  opacity: 0.4;
  filter: blur(0px);
  animation: 16s fade-in-bg;
}
@keyframes fade-in-bg {
  0% {opacity: 0;}
  35% {opacity: 1;}
  50% {opacity: 1;}
  100% {opacity: 0.4;}
}
/* --- Routes Screen --- */
@media only screen and (max-width: 450px) {
  .routesBackground::before {
    display: none!important;
  }
}
.routesBackground::before {
  height: 100vh;
  width: 100%;
  content: "";
  background-image: url('./images/tech.jpg');
  background-size: cover;
  position: absolute;
  top: 100vh;
  right: 0px;
  bottom: 0px;
  left: 0px;
  mask-image: linear-gradient(to bottom,
  rgba(0,0,0,0),
  rgba(0,0,0,1),
  rgba(0,0,0,1),
  rgba(0,0,0,0));
  opacity: 0.2;
  animation: come-in-bg 4s;
}
@keyframes come-in-bg {
  0% { opacity: 0; transform: scale(2);}
  80% { opacity: 0.05;}
  100% { opacity: 0.2; transform: scale(1);}
}

/* --- goHome --- */
.dummy {
  height: 1vh;
  background-color: rgba(0,0,0,0);
  animation: go-home 2s ease-out;
}
@keyframes go-home {
  from {height: 200vh;}
  to {height: 1vh;}
}