.App {
  text-align: center;
}
.App-logo {
  height: 25vh;
  pointer-events: none;
}
.logo {
  height: 5vh;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo, .logo {
    animation: App-logo-spin infinite 10s linear;
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
